body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 20px;
  background-image: url(https://drive.google.com/uc?export=view&id=1mgAx_RIC8nmqTmK9PE8L105pjlYjg3df);
}

ol,
ul {
  padding-left: 30px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
}

.square {
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  height: 34px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 34px;
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: flex;
  flex-direction: row;
}

.game-info {
  margin-left: 20px;
}

h1,
h2 {
  color: white;
}

button {
  border-color: pink;
}

.delete {
  width: 60px;
  font-size: 12px;
  background: #dda0dd;
  border-color: #ba55d3;
}
